import React, {useState} from 'react'
import { QuestionData } from '../asset/data/QuestionData';
import styled from 'styled-components';
import topBar from '../asset/topBar.png'
import { useNavigate,createSearchParams } from "react-router-dom";
import './Main.css';


const Game = () => {

    const navigate = useNavigate();


    const [questionNo, setQuestionNo] = useState(0);
    
    const [score, setScore] = useState(0);


    const handleClickBtn = (select ,answer) => {
        const newScore = () =>{
            return select == answer ? score+1 : score;
        }
        setScore(newScore);

        if(QuestionData.length !== questionNo + 1){
            // 다음문제로 넘어가게 +1 해줌
            setQuestionNo(questionNo+1);

        }else{

            navigate({
                pathname: "/Result",
                search: `?${createSearchParams({// ~/resutl?mbti=ESTJ get 요청으로 데이터를 넘길 수 있음
                  score: score,
                })}`,
              });
        
        }
    }

    return(

        <div className="gamePage">
            <div className="topBar">Couple Game</div>
            <Container>
                <Progress width={(questionNo/QuestionData.length)*100 + "%"} />
            </Container>
            <Title> {QuestionData[questionNo].title} </Title>
            <ButtonGroup>
                    <button 
                        variant="outline-success" 
                        className="selectBtn"
                        onClick={() => handleClickBtn(1,QuestionData[questionNo].answer)}
                    >{QuestionData[questionNo].answerA}
                    </button>

                    <button
                    variant="outline-success" 
                    className="selectBtn"
                    onClick={() => handleClickBtn(2,QuestionData[questionNo].answer)}
                    >{QuestionData[questionNo].answerB}
                    </button>

                    <button
                    variant="outline-success" 
                    className="selectBtn"
                    onClick={() => handleClickBtn(3,QuestionData[questionNo].answer)}
                    >{QuestionData[questionNo].answerC}
                    </button>

                    <button
                    variant="outline-success" 
                    className="selectBtn"
                    onClick={() => handleClickBtn(4,QuestionData[questionNo].answer)}
                    >{QuestionData[questionNo].answerD}
                    </button>
                </ButtonGroup>
        </div>


    )
}


export default Game


const Title = styled.div`
    margin: 50px auto;
    font-size: 20pt;
    width : 90%;
    margin-top:40px;
    display: flex;
    justify-content:center;
    align-items:center;
    border: 2px solid #8F8F8F;
    background-color : #D9D9D9;
    padding:10px;
    font-family:"game-font";
    `

const Progress = styled.div`
  background-color: blue;
  width: ${props => props.width};
  height: 90%;
  transition: width 1s;
  border-radius: 20px;
`;

const Container = styled.div`
  margin: 20px auto;
  background-color: #eee;
  width: 95%;
  height: 30px;
  display: flex;
  align-items: center;
  border-radius: 20px;
`;
const ButtonGroup = styled.div`
    display: flex;
    justify-content:center;
    align-items:center;
    flex-direction:column;
    font-family:"game-font";
`