import React, {useState} from 'react'
import { useNavigate } from 'react-router-dom';
import './Main.css';
import PangImage from '../asset/main.png'

const Home = () => {

    
    const navigate = useNavigate();
    
    const [text, setText] = useState("이름을 입력하세요");

    function onChangetext (e){
        setText(e.target.value);
    }
    
    function onClicktext (e){
        setText('');
    }

    function btnClick(e){
        window.location.href = "/Game"
    }

    return(
        
        <content className="homeCont">
            <img alt="강아지똥" className='mainImg' src={PangImage}></img>
            <button className='homeBtn' onClick={btnClick}>시작</button>
        </content>
        
    )
}

export default Home; 


